import service from './service'

/**
 * 分词测试
 */
export function segment (data) {
  return service({
    method: 'post',
    url: '/dic/seg',
    data
  })
}

/**
 * 获取登录者的权限信息
 */
export function getDicInfoMap (data) {
  return service({
    method: 'get',
    url: '/dic/info'
  })
}

/**
 * 新增字典
 */
export function addDic (data) {
  return service({
    method: 'post',
    url: '/dic/add',
    data
  })
}

/**
 * 请求字典详情
 */
export function getDicDetail (dic, search, pageNum, pageSize) {
  /* 哪个字典，当前页， 每页数量 */
  const data = {
    dic,
    search,
    pageNum,
    pageSize
  }
  return service({
    method: 'post',
    url: '/dic/detail',
    data
  })
}

/**
 * 删除字典某个词
 */
export function dicWord (data, operate = 'post') {
  /* oper可为delete/put */
  return service({
    method: operate,
    url: '/dic/word',
    data
  })
}

/**
 * 下载字典
 */
export function downloadDic (params) {
  /* oper可为delete/put */
  return service({
    method: 'get',
    url: '/file/download',
    params,
    responseType: 'blob'
  })
}

/**
 * 上传字典
 */
export function uploadDic (data) {
  /* oper可为delete/put */
  return service({
    method: 'post',
    url: '/file/upload',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}
