<template>
  <div>
    <el-row>
      <el-card>
        <el-row :gutter="20">
          <el-col :span="4" :offset="2">
            <div class="item">
              <span class="text">字典:</span>
              <el-select v-model="dic" placeholder="请选择" @change="selectDic">
                <el-option
                  v-for="item in dics"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="item">
              <span class="text">模糊匹配:</span>
              <el-input
                placeholder="请输入搜索项"
                v-model="search"
                @keydown.enter.native="beginSearch">
              </el-input>
            </div>
          </el-col>
          <!--         下载文件 -->
          <el-col :span="2" :offset="7">
            <el-button type="primary" @click="downloadFile">下载字典<i class="el-icon-download el-icon--right"></i>
            </el-button>
          </el-col>
          <!--         上传文件 -->
          <el-col :span="4" :offset="1">
            <el-upload
              ref="uploadRef"
              class="upload-demo"
              action=""
              :http-request="uploadFile"
              :show-file-list="true"
              :limit="1"
              :on-exceed="handleExceed"
              :before-upload="beforeAvatarUpload">
              <el-button type="primary">上传字典<i class="el-icon-upload el-icon--right"></i></el-button>
              <div slot="tip" class="el-upload__tip">只能上传txt文件，且不超过2Mb</div>
            </el-upload>
          </el-col>
        </el-row>
      </el-card>
    </el-row>
    <el-row>
      <el-card>
        <el-table
          :data="dicData"
          border
          style="width: 100%">
          <el-table-column
            prop="word"
            label="词">
          </el-table-column>
          <el-table-column
            prop="posf"
            label="词性标签"><!--            part of speech flag-->
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <!--              <el-button v-if="scope.row.posf !== '无'"
                                       size="mini"
                                       type="primary"
                                       icon="el-icon-edit"
                                       :disabled="disableEdit"
                                       @click="handleEdit(scope, scope.row)">
                            </el-button>-->
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click="handleDelete(scope.$index, scope.row)">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--        按钮-->
        <el-row type="flex" justify="end">
          <!--          <el-col>
                      <el-button type="primary">提交更新</el-button>
                    </el-col>-->
          <el-col :offset="24">
            <!-- 分页区域 -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                           :current-page="queryInfo.pageNum" :page-sizes="[5, 10, 15, 20]"
                           :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper"
                           :total="total" background>
            </el-pagination>
          </el-col>
        </el-row>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import { dicWord, downloadDic, getDicDetail, getDicInfoMap, uploadDic } from '@/api/api'

export default {
  name: 'Watch',
  data () {
    return {
      dic: '',
      dics: [/* {
        value: 'userword',
        label: '名称'
      }, {
        value: 'keyword',
        label: '关键词'
      } */],
      poss: [/* {
        value: 'userword',
        label: '名称'
      }, {
        value: 'keyword',
        label: '关键词'
      } */],
      dicData: [/* {
        word: '2016-05-02',
        posf: '王小虎2'
      }, {
        word: '2016-05-04',
        posf: '王小虎4'
      }, {
        word: '2016-05-01',
        posf: '王小虎1'
      }, {
        word: '2016-05-03',
        posf: '王小虎3'
      } */],
      dicInfoMap: {},
      // 查询参数对象
      queryInfo: {
        pageNum: 1,
        pageSize: 10
      },
      // 总数据条数
      total: 100,
      disableEdit: true,
      // 缓存待上传的字典数据
      upFile: new FormData(),
      search: ''
      // uploadData: {}
    }
  },
  methods: {
    beginSearch () {
      this.queryInfo.pageNum = 1
      this.controlEditBtu()
      this.refreshTableData()
    },
    downloadFile () {
      const dicsObj = this.dics.find((item) => {
        return item.value === this.dic
      })
      downloadDic({
        dic: this.dic,
        label: dicsObj.label
      }).then(({ data }) => {
        // 将文件流转成blob形式
        const blob = new Blob([data], { type: 'text/plain' })
        const filename = dicsObj.label + '.txt'
        // 创建一个超链接，将文件流赋进去，然后实现这个超链接的单击事件
        const elink = document.createElement('a')
        elink.download = filename
        elink.style.display = 'none'
        elink.href = URL.createObjectURL(blob)
        document.body.appendChild(elink)
        elink.click()
        URL.revokeObjectURL(elink.href)// 释放URL对象
        document.body.removeChild(elink)
      })
    },
    uploadFile () {
      this.upFile.append('dic', this.dic)
      const dicsObj = this.dics.find((item) => {
        return item.value === this.dic
      })
      this.upFile.append('label', dicsObj.label)
      uploadDic(this.upFile).then(({ data }) => {
        // 清理文件列表
        this.$refs.uploadRef.clearFiles()
        if (data.flag) {
          this.$message.success(data.message)
          this.refreshTableData()
        } else {
          this.$message.error(data.message)
        }
      })
    },
    beforeAvatarUpload (file) {
      console.log(file)
      const isTxt = file.type === 'text/plain'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isTxt) {
        this.$message.error('上传字典只能是 Txt 格式！')
      }
      if (!isLt2M) {
        this.$message.error('上传字典大小不能超过 2MB！')
      }
      // 设置文件数据到upfile
      if (isTxt && isLt2M) {
        this.upFile.append('file', file)
      }
      return isTxt && isLt2M
    },
    handleExceed (files, fileList) {
      console.log(fileList)
      this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`)
    },
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
      dicWord({
        dic: this.dic,
        word: row.word
      }, 'delete').then(({ data }) => {
        console.log(JSON.stringify(data))
        if (data.code === 20000) {
          this.$message.success(data.message)
          this.refreshTableData()
        }
      })
    },
    handleSizeChange (newSize) {
      this.queryInfo.pageSize = newSize
      console.log(newSize)
      this.refreshTableData()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pageNum = newPage
      console.log(newPage)
      this.refreshTableData()
    },
    getDicInfo () {
      /* 发起请求 */
      getDicInfoMap(null).then(({ data }) => {
        if (data.code === 20000) {
          this.dicInfoMap = data.data
          Object.values(data.data).forEach((item) => {
            this.dics.push({
              label: item.label,
              value: item.value
            })
          })
          // 初始化更新表格数据
          this.initTable()
        } else {
          return this.$message.error('获取字典info信息失败')
        }
      })
    },
    selectDic () {
      this.search = ''
      this.controlEditBtu()
      this.refreshTableData()
    },
    controlEditBtu () {
      const posObj = this.dicInfoMap[this.dic]
      // const dicsObj = this.dics.find((item) => {
      //   return item.value === this.dic
      // })
      // this.uploadData = {
      //   dic: this.dic,
      //   label: dicsObj.label
      // }
      /* 设置词性 */
      console.log(JSON.stringify(posObj))
      /* 设置词性 */
      const partOfSpeechMap = posObj.partOfSpeechMap
      if (Object.keys(partOfSpeechMap).length > 1) {
        this.disableEdit = false
      } else {
        this.disableEdit = true
      }
    },
    refreshTableData () {
      getDicDetail(this.dic, this.search, this.queryInfo.pageNum, this.queryInfo.pageSize).then(({ data }) => {
        if (data.code === 20000) {
          console.log(JSON.stringify(data.data))
          this.dicData = []
          this.total = data.data.total
          Object.entries(data.data.dicData).forEach((item) => {
            this.dicData.push({
              word: item[0],
              posf: item[1] === '' ? '无' : item[1]
            })
          })
        } else {
          return this.$message.error('请求字典详情失败!')
        }
      })
    },
    initTable () {
      this.dic = this.dics[0].value
      this.controlEditBtu()
      this.refreshTableData()
    }
  },
  created () {
    this.getDicInfo()
  }
}
</script>

<style lang="less" scoped>
.el-row {
  margin-top: 20px;
}

.right {
  display: flex;
  float: right;
}

.item{
  display: flex;
}

.hlabel{
  width: 100px;
  text-align: center;
}

.text {
  width: 100px;
  font-size: 16px;
  font-family: "Microsoft YaHei";
  text-align: end;
  padding-top: 10px;
  padding-right: 5px;
}
</style>
