<template>
  <div>
    <el-row :gutter="20">
      <el-card>
        <el-row :gutter="20">
          <el-col :span="15" :offset="5">
            <el-checkbox v-model="stopwords" border checked>保留停用词</el-checkbox>
            <el-checkbox v-model="tag" border checked>词性标注</el-checkbox>
            <el-checkbox v-model="refine" disabled border checked>refine</el-checkbox>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="3" :offset="5">
            <el-select v-model="algorithm" placeholder="请选择">
              <el-option
                v-for="item in algorithms"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="10">
            <el-input v-model="input" placeholder="请输入内容" @keyup.enter.native="submit"></el-input>
          </el-col>
          <el-col :span="2">
            <el-button type="primary" style="margin-left: 10%;width: 90%;" @click="submit">确定</el-button>
          </el-col>
        </el-row>
      </el-card>
    </el-row>
    <el-row :gutter="20">
      <el-card>
        <el-col :span="15" :offset="5">
          <el-input class="inputBut" type="textarea" rows="30" v-model="word" readonly></el-input>
        </el-col>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import { segment } from '@/api/api'

export default {
  name: 'Test',
  data () {
    return {
      stopwords: false,
      tag: false,
      refine: false,
      input: '',
      word: '',
      algorithms: [{
        value: 'MaximumMatching',
        label: '正向最大匹配'
      }, {
        value: 'ReverseMaximumMatching',
        label: '逆向最大匹配'
      }, {
        value: 'MinimumMatching',
        label: '正向最小匹配'
      }, {
        value: 'ReverseMinimumMatching',
        label: '逆向最小匹配'
      }, {
        value: 'BidirectionalMaximumMatching',
        label: '双向最大匹配'
      }, {
        value: 'BidirectionalMinimumMatching',
        label: '双向最小匹配'
      }, {
        value: 'BidirectionalMaximumMinimumMatching',
        label: '双向最大最小匹配'
      }, {
        value: 'FullSegmentation',
        label: '全切分'
      }, {
        value: 'MinimalWordCount',
        label: '最少词数'
      }, {
        value: 'MaxNgramScore',
        label: '最大Ngram分值'
      }],
      algorithm: 'MaximumMatching'
    }
  },
  methods: {
    submit () {
      if (this.input.trim().length === 0) {
        return this.$message.info('请先输入待分的句子')
      }
      const data = {
        input: this.input,
        algorithm: this.algorithm,
        tag: this.tag,
        stopwords: this.stopwords,
        refine: this.refine
      }

      segment(data).then(({ data }) => {
        if (data.code !== 20000) {
          return this.$message.error(data.message)
        }
        this.word = data.data
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-row {
  margin-top: 20px;
}

.inputBut {
  padding-bottom: 20px;
}
</style>
