<template>
  <div>
    <el-row>
      <el-card>
        <el-row :gutter="20">
          <el-col :span="4" :offset="2">
            <div class="item">
              <span class="text">字典:</span>
              <el-select v-model="dic" placeholder="请选择" @change="selectDic">
                <el-option
                  v-for="item in dics"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <!--         词性 -->
          <el-col :span="4">
            <div class="item">
              <span class="text">词性标签:</span>
              <!--            part of speech-->
              <el-select v-model="pos" placeholder="请选择">
                <el-option
                  v-for="item in poss"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                  <span style="float: left">{{ item.label }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                </el-option>
              </el-select>
            </div>
          </el-col>
        </el-row>
      </el-card>
    </el-row>
    <el-row>
      <el-card>
        <el-input
          type="textarea"
          :rows="30"
          placeholder="请输入关键词"
          v-model="keywords">
        </el-input>
        <el-row>
          <el-col>
            <el-button type="primary" @click="addDics">新增</el-button>
          </el-col>
        </el-row>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import { addDic, getDicInfoMap } from '@/api/api'

export default {
  name: 'Add',
  data () {
    return {
      keywords: '',
      dic: '',
      dics: [/* {
        value: 'userword',
        label: '名称'
      }, {
        value: 'keyword',
        label: '关键词'
      } */],
      /* part of speech */
      pos: '',
      poss: [/* {
        value: 'userword',
        label: '名称'
      }, {
        value: 'keyword',
        label: '关键词'
      } */],
      dicInfoMap: {}
    }
  },
  methods: {
    getDicInfo () {
      /* 发起请求 */
      getDicInfoMap(null).then(({ data }) => {
        if (data.code === 20000) {
          this.dicInfoMap = data.data
          Object.values(data.data).forEach((item) => {
            this.dics.push({
              label: item.label,
              value: item.value
            })
          })
        } else {
          return this.$message.error('获取字典info信息失败')
        }
      })
    },
    selectDic () {
      const posObj = this.dicInfoMap[this.dic]
      /* 设置词性 */
      this.pos = ''
      const partOfSpeechMap = posObj.partOfSpeechMap
      // console.log(JSON.stringify(partOfSpeechMap))
      this.poss = []
      for (const posItem in partOfSpeechMap) {
        this.poss.push({
          label: posItem,
          value: partOfSpeechMap[posItem]
        })
      }
      if (this.poss.length > 0) {
        this.pos = this.poss[0].value
      }
    },
    addDics () {
      if (this.keywords.trim() === '') {
        return this.$message.error('请先填写要新增的关键词')
      }
      const lines = this.keywords.trim().split(/[(\r\n)\r\n]+/)
      console.log(JSON.stringify(lines))
      // 更新新词
      if (this.dic === '' || (this.poss.length > 0 && this.pos === '')) {
        return this.$message.error('请先选择对应字典和词性标签')
      }
      // 获取选中的词性label
      const possObj = this.poss.find((item) => {
        return item.value === this.pos
      })
      const dicsObj = this.dics.find((item) => {
        return item.value === this.dic
      })
      addDic({
        dic: this.dic,
        label: dicsObj.label,
        pos: this.pos === '' ? '' : possObj.label,
        lines: lines
      }).then(({ data }) => {
        this.$message.success(data.message)
      })
    }
  },
  created () {
    this.getDicInfo()
  }
}
</script>

<style lang="less" scoped>
.el-row {
  margin-top: 20px;
}

.right {
  //align-items: flex-end;
  text-align: right;
}

.item{
  display: flex;
}

.text {
  width: 100px;
  font-size: 16px;
  font-family: "Microsoft YaHei";
  text-align: end;
  padding-top: 10px;
  padding-right: 5px;
}
</style>
